import React from "react";
import Box from "../Box/Box";
import Button from "../Button/Button";
import Flexbox from "../Flexbox/Flexbox";
import PlaintText from "../PlainText/PlaintText";
import Typography from "../Typography/Typography";

interface ProjectPanelProps {
  title?: string;
  descr?: string;
  gitLink?: string;
  hostLink?: string;
  technologieStack?: string[];
}

export default function ProjectPanel(props: ProjectPanelProps) {
  return (
    <React.Fragment>
      <Box padding={2} marginTop={3} width="50rem" borderLeft={2}>
        <Box marginBottom={4}>
          <Typography size="h3" textAlign="start">{props.title ? props.title : ""}</Typography>
        </Box>
        <Box marginBottom={2}>
          <PlaintText>{props.descr ? props.descr : ""}</PlaintText>
        </Box>

        <Flexbox width="100%" justify="space-between">
         <Box width="70%"> <PlaintText color="secondary">
            {props.technologieStack
              ? `Technologies: ${props.technologieStack?.join(", ")}`
              : ""}
          </PlaintText>
          </Box>
          <Flexbox>
            <a href={props.hostLink} target="_blank" rel="noopener noreferrer">
              <Button>Хостинг</Button>
            </a>
            <a href={props.gitLink} target="_blank" rel="noopener noreferrer">
              {" "}
              <Button color="secondary">git</Button>
            </a>
          </Flexbox>
        </Flexbox>
      </Box>
      <Flexbox width="100%">
        <Box marginTop={2} borderBottom={1} width="20rem" maxWidth="50%"></Box>
      </Flexbox>
    </React.Fragment>
  );
}
