import React from "react";
import styled, { DefaultTheme } from "styled-components";

interface IBoxProps {
  children?: any;
  theme?: DefaultTheme;
  margin?: 0 | 1 | 2 | 3 | 4;
  marginLeft?: 0 | 1 | 2 | 3 | 4;
  marginRight?: 0 | 1 | 2 | 3 | 4;
  marginTop?: 0 | 1 | 2 | 3 | 4;
  marginBottom?: 0 | 1 | 2 | 3 | 4;
  padding?: 0 | 1 | 2 | 3 | 4;
  paddingLeft?: 0 | 1 | 2 | 3 | 4;
  paddingRight?: 0 | 1 | 2 | 3 | 4;
  paddingTop?: 0 | 1 | 2 | 3 | 4;
  paddingBottom?: 0 | 1 | 2 | 3 | 4;
  border?: 0 | 1 | 2 | 3 | 4;
  borderLeft?: 0 | 1 | 2 | 3 | 4;
  borderRight?: 0 | 1 | 2 | 3 | 4;
  borderTop?: 0 | 1 | 2 | 3 | 4;
  borderBottom?: 0 | 1 | 2 | 3 | 4;
  borderRadius?: 0 | 1 | 2 | 3 | 4;
  maxWidth?:string;
  width?: string;
  height?:string;
  background?: "default" | "dark" | "light" | "none";
  id?:string;
}

interface IStyledBoxProps {
  theme?: DefaultTheme;
  margin: 0 | 1 | 2 | 3 | 4;
  marginLeft: 0 | 1 | 2 | 3 | 4;
  marginRight: 0 | 1 | 2 | 3 | 4;
  marginTop: 0 | 1 | 2 | 3 | 4;
  marginBottom: 0 | 1 | 2 | 3 | 4;
  padding: 0 | 1 | 2 | 3 | 4;
  paddingLeft: 0 | 1 | 2 | 3 | 4;
  paddingRight: 0 | 1 | 2 | 3 | 4;
  paddingTop: 0 | 1 | 2 | 3 | 4;
  paddingBottom: 0 | 1 | 2 | 3 | 4;
  border: 0 | 1 | 2 | 3 | 4;
  borderLeft: 0 | 1 | 2 | 3 | 4;
  borderRight: 0 | 1 | 2 | 3 | 4;
  borderTop: 0 | 1 | 2 | 3 | 4;
  borderBottom: 0 | 1 | 2 | 3 | 4;
  borderRadius: 0 | 1 | 2 | 3 | 4;
  maxWidth:string;
  width: string;
  height:string;
  background: "default" | "dark" | "light" | "none";
}
const StyledBox = styled.div<IStyledBoxProps>`
  display: block;
  border: solid;
  border-width: 0;
  border-color: black;
  width:${(props) => props.width};
  max-width: ${(props) => props.maxWidth};
  height: ${(props) => props.height};
  box-sizing: border-box;
  margin: ${(props) => {
    return props.margin === 0
      ? ""
      : props.theme.spacing.margin[`m${props.margin}`];
  }};
  margin-left: ${(props) =>
    props.marginLeft === 0
      ? ""
      : props.theme.spacing.margin[`m${props.marginLeft}`]};
  margin-right: ${(props) =>
    props.marginRight === 0
      ? ""
      : props.theme.spacing.margin[`m${props.marginRight}`]};
  margin-top: ${(props) =>
    props.marginTop === 0
      ? ""
      : props.theme.spacing.margin[`m${props.marginTop}`]};
  margin-bottom: ${(props) =>
    props.marginBottom === 0
      ? ""
      : props.theme.spacing.margin[`m${props.marginBottom}`]};

  padding: ${(props) =>
    props.padding === 0
      ? ""
      : props.theme.spacing.padding[`p${props.padding}`]};
  padding-left: ${(props) =>
    props.paddingLeft === 0
      ? ""
      : props.theme.spacing.padding[`p${props.paddingLeft}`]};
  padding-right: ${(props) =>
    props.paddingRight === 0
      ? ""
      : props.theme.spacing.padding[`p${props.paddingRight}`]};
  padding-bottom: ${(props) =>
    props.paddingBottom === 0
      ? ""
      : props.theme.spacing.padding[`p${props.paddingBottom}`]};
  padding-top: ${(props) =>
    props.paddingTop === 0
      ? ""
      : props.theme.spacing.padding[`p${props.paddingTop}`]};

  border-width: ${(props) =>
    props.border === 0 ? "" : props.theme.borders[`w${props.border}`]};
  border-radius: ${(props) =>
    props.borderRadius === 0
      ? ""
      : props.theme.borders[`r${props.borderRadius}`]};
  border-left:  ${(props) =>
    props.borderLeft === 0 ? "" :`solid `+ props.theme.borders[`w${props.borderLeft}`]};
  border-right: ${(props) =>
    props.borderRight === 0
      ? ""
      : `solid `+props.theme.borders[`w${props.borderRight}`]};
  border-bottom: ${(props) =>
    props.borderBottom === 0
      ? ""
      : `solid `+props.theme.borders[`w${props.borderBottom}`]};
  border-top: ${(props) =>
    props.borderTop === 0 ? "" : `solid `+props.theme.borders[`w${props.borderTop}`]};
  background: ${(props) =>
    props.background === "none"
      ? ""
      : props.theme.colors.background[props.background]};
`;

export default function Box(props: IBoxProps) {
  return (
    <StyledBox
      margin={0}
      padding={0}
      border={0}
      borderRadius={0}
      marginLeft={0}
      marginRight={0}
      marginTop={0}
      marginBottom={0}
      paddingBottom={0}
      paddingLeft={0}
      paddingRight={0}
      paddingTop={0}
      borderBottom={0}
      borderLeft={0}
      borderRight={0}
      borderTop={0}
      maxWidth="100%"
      width="100%"
      background="none"
      height="fit-content"
      {...props}
    >
      {props.children}
    </StyledBox>
  );
}
