import React, { useState } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import Box from "../Box/Box";
import Flexbox from "../Flexbox/Flexbox";
import HamburgerButton from "../HamburgerButton/HamburgerButton";

interface ISidebarProps {
  names?: string[];
  links?: string[];
}

const StyledSidebarContainer = styled.div`
  position: relative;
  width: fit-content;
  height: 100vh;
  background: ${(props) => props.theme.colors.secondary.main};
  color: ${(props) => props.theme.colors.secondary.contrastText};
  right: 0;
  top: 0;
  transition: all ease-in-out 0.2s;
  marginright: 0px;
`;

const StyledSidebarRoot = styled.div`
  position: fixed;
  width: fit-content;
  height: 100vh;

  right: 0;
  top: 0;
  z-index: 2;
`;
const StyledSidebarItem = styled.div`
  font-size: ${(props) => props.theme.typography.h3.fontSize};
  width: 100%;
  text-align: center;
  transition: all ease-in-out 0.2s;
  margin-bottom: 1.25rem;
  cursor: pointer;
  &:after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background: black;
    transition: all ease-in-out 0.2s;
  }
  &:hover {
    color: darkorange;
    &:after {
      background: darkorange;
    }
  }
`;
export default function Sidebar(props: ISidebarProps) {
  const { links, names } = props;
  const elements = [];
  if (links && names) {
    for (let i = 0; i < links.length; i++) {
      elements.push(
        <Link to={links[i]}>
          <StyledSidebarItem>{names[i]}</StyledSidebarItem>
        </Link>
      );
    }
  }
  const [isOpen, setIsOpen] = useState(false);
  return (
    <StyledSidebarRoot>
      <Flexbox justify="end" height="100%" align="start">
        <Flexbox direction="column" justify="start">
          <Box paddingTop={3} paddingRight={3}>
            <HamburgerButton
              onClick={() => {
                setIsOpen(!isOpen);
              }}
            ></HamburgerButton>
          </Box>
        </Flexbox>
        <StyledSidebarContainer
          style={!isOpen ? { marginRight: "-160px" } : {}}
        >
          <Box padding={3}>
            <Flexbox direction="column" justify="start">
              {elements}
            </Flexbox>
          </Box>
        </StyledSidebarContainer>
      </Flexbox>
    </StyledSidebarRoot>
  );
}
