import React from "react";
import styled, { DefaultTheme } from "styled-components";

interface IPlainTextProps {
  children?: any;
  theme?: DefaultTheme;
  color?: "primary" | "secondary" | "disabled";
}
interface IStyledPlainTextProps {
  theme?: DefaultTheme;
  color: "primary" | "secondary" | "disabled";
}
const StyledPlainText = styled.p<IStyledPlainTextProps>`
  font-size: ${(props) => props.theme.typography.body.fontSize};
  color: ${(props) => props.theme.colors.text[props.color]};
`;

export default function PlaintText(props: IPlainTextProps) {
  return (
    <StyledPlainText color="primary" {...props}>
      {props.children}
    </StyledPlainText>
  );
}
