import React from "react";
import styled from "styled-components";

const StyledPageContainer = styled.div`
  position: absolute;
  overflow: hidden;
  width:100%;
  height:100%;
  & div.stripe {
    position: absolute;
    top: 0;
    left: 100%;
    width: 100vw;
    height: 10%;
    &.main {
      background-color: ${(props) => props.theme.colors.primary.main};
    }
    &.light {
      background-color: ${(props) => props.theme.colors.primary.light};
    }
    &.dark {
      background-color: ${(props) => props.theme.colors.primary.dark};
    }
  }
`;
interface IPageContainerProps {
  children?: any;
}
export default function PageContainer(props: IPageContainerProps) {
  return (
    <StyledPageContainer>
      {props.children}
      <div className="stripe main"></div>
      <div className="stripe light" style={{ top: "10%" }}></div>
      <div className="stripe dark" style={{ top: "20%" }}></div>
      <div className="stripe main" style={{ top: "30%" }}></div>
      <div className="stripe light" style={{ top: "40%" }}></div>
      <div className="stripe dark" style={{ top: "50%" }}></div>
      <div className="stripe main" style={{ top: "60%" }}></div>
      <div className="stripe light" style={{ top: "70%" }}></div>
      <div className="stripe dark" style={{ top: "80%" }}></div>
      <div className="stripe main" style={{ top: "90%" }}></div>
      <div className="stripe light" style={{ top: "100%" }}></div>
    </StyledPageContainer>
  );
}
