import { createGlobalStyle } from "styled-components";
const pageTransition = `
.slideIn-appear,
.slideIn-enter {
    opacity:0;
    & div.stripe{
        left:0;
    }
}

.slideIn-appear-active,
.slideIn-enter.slideIn-enter-active {
    opacity:1;
    transition:opacity 0ms linear 1100ms;
    & div.stripe{
        
        left:100%;
        transition:left 600ms linear 1100ms;
    }
}


.slideIn-exit {
    opacity:1;
   & div.stripe{
       left:-100%;
   }
}
.slideIn-exit.slideIn-exit-active {
    opacity:0;
    transition:opacity 0ms linear 1200ms;
    & div.stripe{
        left:0;
        transition:left 600ms linear;
    }
}
`;
export default createGlobalStyle`
body{
    background: ${(props) => props.theme.colors.background.default};
    font-family:'Roboto',Sans-seriff;
    overflow:hidden;
}
html{
    font-size: ${(props) => props.theme.typography.defaultFontSize};
    color:white;
}
a{
    text-decoration:none;
    color:black;
}
${pageTransition}
`;
