import React from "react";
import styled, { DefaultTheme } from "styled-components";
import { ValidColors } from "../../types";
type ButtonVariant="filled" | "outlined" | "text";
type ButtonSize="large" | "medium" | "small";
interface IButtonProps {
  children?: any;
  theme?: DefaultTheme;
  color?: ValidColors;
  variant?: ButtonVariant;
  size?: ButtonSize;
}
interface IStyledButtonProps {
  theme?: DefaultTheme;
  color: ValidColors;
  variant:ButtonVariant;
  size: ButtonSize;
}

const StyledButton = styled.button<IStyledButtonProps>`
  display: inline-block;
  margin: 0.25rem;
  border: solid;
  position: relative;
  border-width: ${(props) => props.theme.borders.w2};
  border-radius: ${(props) => props.theme.borders.r2};
  box-sizing: border-box;
  cursor: pointer;
  overflow: hidden;
  ${(props) => {
    const color = props.theme.colors[props.color];
    let variantStyles = `transition:all ease-in-out 0.2s;`;
    let hoverStyles = "";
    let sizeStyles = "";
    if (props.variant === "filled") {
      variantStyles += `
      color:${color.contrastText}; 
      background-color:${color.main};
      border-color:${color.main};

      `;
      hoverStyles = `
      &:hover{
        background-color:${color.light};
        border-color:${color.light};
      }
      `;
    } else if (props.variant === "outlined") {
      variantStyles += `
      color:${color.main}; 
      border-color:${color.main};
      background:transparent;
      `;
      hoverStyles = `
      &:hover{
        border-color:${color.light};
        color:${color.light}; 
      }
      `;
    } else if (props.variant === "text") {
      variantStyles += ` 
      color:${color.contrastText}; 
      background:none;
      border:transparent;`;
      hoverStyles = `&:after{
        content:'';
        display:block;
        width:auto;
        height:2px;
        background:rgba(255,255,255,0);
        transform:translateX(-100%) translateY(200%);
        transition:all ease-in-out 0.2s;
        
      };
      &:hover{
        &:after{
          background:rgba(255,255,255,1);
          transform:translateX(0) translateY(200%);
        }
      }`;
    }

    sizeStyles = `font-size:${props.theme.button[props.size].fontSize};
      padding:${props.theme.button[props.size].padding};`;

    return `
    ${variantStyles}

${sizeStyles}
${hoverStyles}
    `;
  }};
`;

const defaultValues: IStyledButtonProps = {
  color: "primary",
  variant: "filled",
  size: "medium",
};
export default function Button(props: IButtonProps) {
  const filledProps = { ...defaultValues, ...props };
  return (
    <StyledButton
      {...filledProps}
      
    >
      
      <span>{props.children}</span>
    </StyledButton>
  );
}
