import React from "react";
import { Link } from "react-router-dom";
import Box from "../../Box/Box";
import Button from "../../Button/Button";
import Flexbox from "../../Flexbox/Flexbox";
import Logo from "../../Logo/Logo";
import PageContainer from "../PageContainer/PageContainer";
export default function LandingPage() {
  return (
    <PageContainer>
      <Box height="100vh">
        <Flexbox
          direction="column"
          justify="center"
          align="center"
          height="100%"
          width="100%"
        >
          <div>Ivan Galkin</div>
          <Box width="fit-content" marginBottom={2}>
            front-end developer
          </Box>
          <Logo />
          <Flexbox width="100%">
            <Link to="/projects">
              {" "}
              <Button variant="outlined">Проекты</Button>
            </Link>
            <a href="https://github.com/bjing94/" target="_blank" rel="noopener noreferrer">
              <Button variant="outlined" color="secondary">
                Github
              </Button>
            </a>
          </Flexbox>
        </Flexbox>
      </Box>
    </PageContainer>
  );
}
