import React from "react";
import styled, { DefaultTheme } from "styled-components";

interface ITypographyProps {
  children?: any;
  theme?: DefaultTheme;
  size?: "h1" | "h2" | "h3" | "h4";
  color?: "primary" | "secondary";
  textAlign?:'center'|'start';
}

interface IStyledTypographyProps {
  theme?: DefaultTheme;
  size: "h1" | "h2" | "h3" | "h4";
  color: "primary" | "secondary";
  textAlign:'center'|'start';
}
const StyledTypography = styled.h1<IStyledTypographyProps>`
  font-size: ${(props) => {
    return `${props.theme.typography[props.size].fontSize}`
  }};
  color:${(props) => {
    return `${props.theme.colors.text[props.color]}`;
  }};
  font-weight:${props=>props.theme.typography.fontWeightBold};
  margin:0;
  text-align:${props=>props.textAlign};
`;

const defaultValues: IStyledTypographyProps={
  size: "h3",
  color: "primary",
  textAlign:'center'
}
export default function Typography(props: ITypographyProps) {
  return (
    <StyledTypography {...defaultValues} {...props}>
      {props.children}
    </StyledTypography>
  );
}
