import React from 'react'
import styled from 'styled-components'
const StyledHamburgerButton=styled.button`
    width:3rem;
    height:3rem;
    background:none;
    border:none;
    & div{
        width:100%;
        height:4px;
        background:white;
    }
    & div:not(:first-child){
        margin-top:12px;
    }
`;
export default function HamburgerButton(props:{onClick:()=>void}) {
    return (
        <StyledHamburgerButton onClick={props.onClick}>
            <div></div>
            <div></div>
            <div></div>
        </StyledHamburgerButton>
    )
}
