import React from 'react'
import styled from 'styled-components'

interface IFlexboxProps{
    children?:any;
    direction?:'row'|'column'|'row-reverse'|'column-reverse';
    wrap?:'nowrap'|'wrap'|'wrap-reverse';
    justify?:'start'|'end'|'center'|'space-between'|'space-around'|'space-evenly';
    align?:'start'|'end'|'center'|'stretch';
    width?:string;
    height?:string;
}
const StyledFlexbox=styled.div<IFlexboxProps>`
display:flex;
align-items:${props=>props.align};
justify-content:${props=>props.justify};
flex-direction:${props=>props.direction};
flex-wrap:${props=>props.wrap};
width:${props=>props.width};
height:${props=>props.height};
`;
export default function Flexbox(props:IFlexboxProps) {
    return (
        <StyledFlexbox width={'fit-content'} height="fit-content" align="center" justify="center" direction="row" wrap="wrap" {...props}>
            {props.children}
        </StyledFlexbox>
    )
}
