import React from "react";
import Box from "../../Box/Box";
import ProjectPanel from "../../ProjectPanel/ProjectPanel";
import Typography from "../../Typography/Typography";
import PageContainer from "../PageContainer/PageContainer";
export default function ProjectsPage() {
  return (
    <PageContainer>
      <Box width="100%" height="100vh"  padding={3}>
        <Box background="dark" width="fit-content" padding={3}>
          <Typography size="h3" textAlign="start">{`<Проекты>`}</Typography>
          <Box marginLeft={3} marginTop={2} marginBottom={2}>
            <ProjectPanel
              title="Weather App"
              descr="Simple Weather App based on Open Weather Map API."
              technologieStack={["React", "React-Router", "Material UI"]}
              gitLink="https://github.com/bjing94/WeatherApp"
              hostLink="https://bjing94.github.io/WeatherApp/"
            />
            <ProjectPanel
              title="Image Searcher"
              descr="Web App base on Unisplash API. Allows you to search for images in Unisplash database."
              technologieStack={[
                "React",
                "React-Router",
                "Material UI",
                "Typescript",
              ]}
              gitLink="https://github.com/bjing94/ImageSearcher"
              hostLink="https://bjing94.github.io/ImageSearcher/#/"
            />
          </Box>
          <Typography size="h3" textAlign="start">{`</Проекты>`}</Typography>
        </Box>
      </Box>
    </PageContainer>
  );
}
